.article-page{
    width: 100%;
    height: 100%;
}
.article-container{
    width: 100%;
    height: auto;
    position: relative;
}
.article-heading-container{
    border-bottom: 4px solid rgba(128, 128, 128, 0.342);
    box-shadow: 0px 3px 6px #00000029;
    display: flex;
    justify-content: space-between;
    padding: 1.4rem 4rem 1.5rem 1.6rem;
    height: auto;
    margin-bottom: 2.2rem;
}
.article-heading-container .theSirenContainer{
    text-align: right;
    width:18%;
    margin: 0;
}
.article-heading-container .theSirenContainer .the{
    width: 25px;
    font-weight: 500;
    font-size: 16px;
}
.article-heading-container .theSirenContainer .siren{
    font-size:32px;
    font-weight:700
}

.article-heading-container .get-started{
    border: 1px solid #03A87C;
    font-family: Open Sans;
    color:#03A87C;
    font-size: 18px;
    background-color: white;
    border-radius: 2px;
    width: 7rem;
    height: 2.4rem;
    font-weight: 200;
}

.article-heading-container .get-started:hover{
    background-color: #03a87ce1;
    color:white;

}

.article-container .clap-fixed-container{
    position: fixed;
    top: 340px;
    left: 110px;
}

@media screen and (min-width:320px) and (max-width:720px) {
    .article-heading-container{
        padding: 0.7rem;
    }
    .article-heading-container .theSirenContainer {
        width:39%
    }

    .clap-fixed-container{
        display: none;

    }

    
}